import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Button } from 'antd';
import { partyMem } from 'dump/data';
import { TableData } from 'components/common/index';
import { getStudentsForPartyMem } from 'services/studentForPartyMem';
import FormSerchStuForPartyMem from './FormSerchStuForPartyMem';
import './style.scss';

class StuPartyMemPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  handleSearch = values => {
    this.setState({ paramSearch: values });
    console.log(values);
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldName',
        key: 'studyFieldName',
      },
      {
        title: 'Khóa học',
        dataIndex: 'courseName',
        key: 'courseName',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'facultyName',
        key: 'facultyName',
      },
      {
        title: 'Tên hệ',
        dataIndex: 'trainTypeName',
        key: 'trainTypeName',
      },
      {
        title: 'Ngày kết nạp',
        dataIndex: 'politicalDate',
        key: 'politicalDate',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          partyMem.length >= 1 ? (
            <Fragment>
              <Button
                className="btnNoiNgoaiTru"
                type="primary"
                href={`#party-mem/form/${record.code}`}
              >
                Kết nạp
              </Button>
              <Button className="btnNoiNgoaiTru" type="primary">
                Khai trừ
              </Button>
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Card title="Đảng viên là sinh viên">
        <Row>
          <Col span={24}>
            <FormSerchStuForPartyMem onSubmit={this.handleSearch} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getStudentsForPartyMem}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuPartyMemPage />
  </Page>
);



