import { request } from 'utils';

// request.prefix = '/api/categories';
const prefix = '/api/students';

export const getStudentsForPartyMem = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Student/GetPoliticStudents',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studentCode,
      studentFullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createStudentTitles = ({ label }) =>
  request({
    url: '/StudentTitle',
    data: { label },
    method: 'POST',
  });

export const deleteStudentTitles = id =>
  request({
    url: `/StudentTitle/${id}`,
    method: 'DELETE',
  });

export const updateStudentTitles = ({ id, label }) =>
  request({
    url: `/StudentTitle/${id}`,
    data: { id, label },
    method: 'PUT',
  });

export const getStudentTitlesById = id =>
  request({
    url: `/StudentTitle/${id}`,
    method: 'GET',
  });



