import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'FormSerchStuForPartyMem' })
class FormSerchStuForPartyMem extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.props.onSubmit(values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Mã SV">{getFieldDecorator('code', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên">{getFieldDecorator('fullname', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label=" ">
              {getFieldDecorator('btnEvent', {})(
                <div className="btn-group">
                  <BtnSearch htmlType="submit" />
                  <BtnClear />
                </div>
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSerchStuForPartyMem;



